@import "resources/sass/user/variables";
#catalog {

  .form-check-input[type=checkbox]:checked {
    background-color: $danger;
    border-color: $danger;

    &:focus {
      background-color: $danger;
      &:before {
        box-shadow: 0 0 0 13px $danger;
      }
    }
  }


  &> div {
    display: flex;
    align-items: center;
    margin-top: 20px;

    > .title {
      font-size: 1.1em;
      font-weight: 700;
      color: $dark;
      margin-left: .1em;

      @include respond-to(md) {
        font-size: 1.5em;
      }
    }

    .badge {
      line-height: unset;
      background: $white;
      color: $dark;
      font-weight: 400;
      margin-left: 1em;
    }

    .badge-notification {
      line-height: unset;
      background: $white;
      color: $dark;
      font-weight: 500;
      position: absolute;
      right: -3px;
      top: 0;
      font-size: 0.6em;
      padding: 0 4px;
    }
  }

  #product-all {
    display: flex;
    align-items: center;

    .dropdown {
      display: flex;

      .dropdown-toggle {
        display: flex;
        align-items: center;
        width: 100%;

        &:after {
          color: $gray-2;
          margin-left: auto;
          font-family: "Font Awesome 5 Pro", serif;
          content: "\F078";
          border: none;
          text-align: center;
          display: flex;
          align-self: center;
          padding: 0 0 0 5px;
        }
      }

      .dropdown-menu {
        width: 100vw!important;
        transform: translate(0, 40px)!important;
        margin-left: -12px;



        @include respond-to(sm) {
          width: 300px!important;
          max-height: 300px!important;
          margin-left: unset;
        }

        .checkbox {
          margin-top: 5px;
          margin-bottom: 5px;
        }
      }
    }
  }

  hr {
    color: $gray;
  }

  .filter-badge {
    display: flex;
    align-items: center;
    background: #E9EAEC;
    border-radius: 10px;

    button {
      display: flex;
      align-items: center;
      margin: 0 0 0 .3em;
    }
  }

  .badge.badge-notification {
    margin-left: 0 !important;
  }

  .clear-filters {
    a {
      color: #2D3134;
      text-decoration: underline;
    }
  }
}
