.modal {
  padding-right: 0 !important;
}

@media screen and (min-width: 650px) {
  .modal-user .modal-dialog {
    min-width: 600px;
  }
}
.modal-user .modal-content {
  border-radius: 0;
  position: relative;
}
.modal-user .modal-content #modal-photo {
  min-height: 100%;
  width: 100%;
  object-fit: cover;
}
.modal-user .modal-content h4, .modal-user .modal-content .h4 {
  line-height: 1.5em;
  font-weight: 800;
}
